
.go-to-login {
  padding-top: 6rem;
}
.go-to-login .photo {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
}
.go-to-login .text {
  margin: 1rem 0;
  font-size: 1.1rem;
  color: #475669;
}
.go-to-login .btn {
  width: 8.125rem;
  font-size: .8rem;
  color: #fff;
  background-color: #41b883;
}

.message {
  -webkit-overflow-scrolling: touch;
}
.message .tip {
  line-height: 2.125rem;
  font-size: .85rem;
  font-weight: normal;
  text-indent: 12px;
  background-color: #eff2f7;
}
.message .tip:before {
  content: "";
  display: block;
  position: absolute;
  height: 100%;
  width: 6px;
  background-color: #d3dce6;
}
.message .tip .set-read-state {
  margin-right: 12px;
  color: #41b883;
}
.message .list .no-msg {
  padding: 14px 0;
  font-size: .9rem;
}
.message .list .item {
  padding: 1rem;
  border-bottom: 1px solid #eff2f7;
  color: #d0d7df;
}
.message .list .item .bar {
  margin-bottom: 0.375rem;
}
.message .list .item .bar .name {
  color: #b1bdcb;
}
.message .mew-msgs .bar .time {
  color: #b1bdcb;
}
.message .mew-msgs .main {
  color: #475669;
}

.topics {
  min-height: 0 !important;
  background-color: #eff2f7;
}
.topics .mu-tabs {
  background-color: #fff;
}
.topics .mu-tabs .mu-tab-link {
  color: rgba(31, 45, 61, 0.7);
}
.topics .tabs-line {
  background-color: #41b883;
}
.topics .topic-list {
  margin: 0;
  -webkit-overflow-scrolling: touch;
}
.topics .topic-list-load {
  padding: 0;
}
.topics .topic-list-load .mu-circle {
  border-color: #41b883;
}
.topics .topic-list-load .mu-infinite-scroll-text {
  margin: .8rem 1rem;
}

.topic-item {
  padding: 1.125rem;
  border-top: .56rem solid #eff2f7;
  background-color: #fff;
}
.topic-item .avatar {
  width: 3.625rem;
  height: 3.625rem;
  border-radius: 50%;
}
.topic-item .title {
  margin: 0 .8rem;
  padding-top: .5rem;
  word-wrap: break-word;
  word-break: break-all;
}
.topic-item .tip {
  display: inline-block;
  -ms-flex-item-align: start;
      align-self: flex-start;
  padding: 0 .3em;
  margin-top: .7rem;
  font-size: .8rem;
  color: #fff;
  background-color: #d3dce6;
  border-radius: 15%;
}
.topic-item .info {
  margin-top: .8rem;
  font-size: .75rem;
  color: #92a8c1;
}
.topic-item .info .time {
  margin-left: .85rem;
}
.topic-item .info .see,
.topic-item .info .reply {
  margin-left: 10px;
}
.topic-item .info .see .mu-icon,
.topic-item .info .reply .mu-icon {
  margin-right: 3px;
}

.no-more-data {
  padding: 1rem 0;
}
.no-more-data .text {
  font-size: 1rem;
  color: #8492a6;
}
.no-more-data .text::before,
.no-more-data .text::after {
  content: "";
  position: absolute;
  top: 50%;
  display: inline-block;
  width: 2.626rem;
  height: 1px;
  background-color: #99a9bf;
}
.no-more-data .text::before {
  left: -3rem;
}
.no-more-data .text::after {
  right: -3rem;
}

.user {
  background-color: #eff2f7;
}

.login {
  max-width: 352px;
}
.login .access-token {
  margin-top: 30px;
}
.login .access-token .lock-icon {
  bottom: -8px;
  padding: 0 10px;
  color: #7a7777;
}
.login .access-token .lock-icon.ac {
  color: #41b883;
}
.login .access-token .mu-text-field {
  width: 200px;
}
.login .access-token .mu-text-field .mu-text-field-focus-line {
  background-color: #41b883;
}
.login .mu-raised-button {
  display: block;
  width: 70%;
  margin-top: 12px;
}
.login .mu-raised-button:nth-child(2) {
  color: #fff;
  background-color: #41b883;
}
.login .how-get {
  width: 22rem;
  margin: 30px auto 15px;
  font-size: 1rem;
  font-weight: bold;
}
.login .how-get .material-icons {
  position: relative;
  top: 2px;
}
.login .tip {
  padding: 0 40px;
  line-height: 1.6;
  font-size: .95rem;
}

.user .user-flex {
  height: 4.375rem;
  margin-top: 1rem;
  padding: 0 12px;
  background-color: #fff;
}
.user .user-flex .user-photo {
  height: 3.125rem;
  width: 3.125rem;
}
.user .user-flex .user-info {
  height: 2.875rem;
  margin-left: 12px;
  font-size: 1rem;
}
.user .user-flex .user-info .user-id {
  max-width: 18rem;
}
.user .user-flex .user-info .user-score {
  top: 0.2rem;
  padding: 1px 6px;
  white-space: nowrap;
  font-size: .85rem;
  background-color: #eff2f7;
  border-radius: 18px;
}
.user .user-flex .user-info .user-score .material-icons {
  line-height: 20px;
  margin-right: 2px;
}
.user .topic-list {
  padding: 0;
  margin: 1rem 0;
  background-color: #fff;
}
.user .topic-list .mu-item {
  position: relative;
  padding-left: 45px;
  line-height: 45px;
  border-bottom: 1px solid #e6eaf2;
}
.user .topic-list .mu-item .mu-item-content {
  font-size: .85rem;
}
.user .topic-list .topic-num {
  position: absolute;
  right: -1rem;
  height: 21px;
  min-width: 21px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background-color: #e5e9f2;
  border-radius: 50%;
}
.user .logout-button {
  height: 45px;
  background-color: #fff;
}

.user-topic {
  background-color: #fff;
}
.user-topic .mu-appbar {
  padding: 0;
}
.user-topic .mu-appbar .mu-appbar-title {
  padding: 0;
}
.user-topic-item {
  padding: 1rem;
  background-color: #fff;
  margin-top: 6px;
}
.user-topic-item:first-of-type {
  margin-top: 0;
}
.user-topic-item .user-photo {
  -ms-flex-item-align: center;
      align-self: center;
  width: 2.8125rem;
  height: 2.8125rem;
  margin-right: 12px;
}
.user-topic-item .title {
  font-size: .9rem;
}
.user-topic-item .bar {
  margin-top: .3rem;
  font-size: .65rem;
  color: #92a8c1;
}
